
import name from '@/assets/image/refractive_archives/name.png'
import passwordIcon from '@/assets/image/refractive_archives/password.png'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import { login } from '@/api/refractive_archives/sessions'
export default {
components: {
CheckIcon
},
data() {
return {
name,
passwordIcon,
demo1: false,
toastText: '',
toastShow: false,
ruleForm: {
userName: '', // 用户名
password: '' 
}
}
},

mounted() {
this.getCookie()
},
methods: {

submitForm(formName) {

var name = this.ruleForm.userName

var pass = this.ruleForm.password
if (name === '' || name == null) {
this.toast('请输入正确的用户名')
return
} else if (pass === '' || pass == null) {
this.toast('请输入正确的密码')
return
}

if (this.checked === true) {

this.setCookie(name, pass, 7)
}
login({ username: name, password: pass }).then(res => {
console.log('res => ', res.data)
this.$store.commit('updateUserInfo', res.data)
this.toast('登录成功')
this.$router.push({ name: 'search_child', query: { data: JSON.stringify() }})
}, error => {
console.log('这是个错误', error)
this.toast(error)
})














},

setCookie(c_name, c_pwd, exdays) {
var exdate = new Date() 
exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays) 

window.document.cookie = 'userName' + '=' + c_name + ';path=/;expires=' + exdate.toGMTString()
window.document.cookie = 'userPwd' + '=' + c_pwd + ';path=/;expires=' + exdate.toGMTString()
},

getCookie: function() {
if (document.cookie.length > 0) {
var arr = document.cookie.split('; ')
for (var i = 0; i < arr.length; i++) {
var arr2 = arr[i].split('=')

if (arr2[0] === 'userName') {
this.ruleForm.userName = arr2[1]
} else if (arr2[0] === 'userPwd') {
this.ruleForm.password = arr2[1]
}
}
}
},

clearCookie: function() {
this.setCookie('', '', -1)
},
toast(str) {
const V = this
V.toastText = str
V.toastShow = true
setTimeout(function() {
V.toastShow = false
}, 1500)
}
}
}



